  &__social {
    padding-top: 30px;
    @media only screen and(max-width: 767px) {
      padding-top: 15px; }
    &__text {
      text-align: right;
      margin-bottom: 3px; }
    &__icons {
      width: 175px;
      margin-left: auto;
      display: flex;
