            width: 12px;
            height: 12px; } } } } }
  &__copyright {
    padding-top: 30px;
    @media only screen and(max-width: 767px) {
      padding-top: 15px; }
    @media only screen and(max-width: 575px) {
      text-align: center; }
    &__text {
      padding-bottom: 15px;
      @media only screen and(max-width: 767px) {
        padding-bottom: 5px; }
      @media only screen and(max-width: 575px) {
        padding-bottom: 0;
        font-size: 8px; } } }
  &__creator {
    padding-top: 20px;
    @media only screen and(max-width: 767px) {
     padding-top: 5px; }
    @media only screen and(max-width: 575px) {
      display: none; }
    &__wrap {
      width: 250px;
      margin-left: auto; }
    &__text {
      display: inline-block; } } }
